import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AcknowledgementListComponent } from './acknowledgement-list.component';


const routes: Routes = [
  {
    path: '',
    component: AcknowledgementListComponent,

  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AcknowledgementListRoutingModule { }
