import { Component, Injector, OnInit } from '@angular/core';
import { URLz } from 'src/app/enums/url.enum';
import { HttpErrorResponse } from '@angular/common/http';
import { BaseListClass } from 'src/app/class/base-list-class';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './acknowledgement-list.component.html',
  styleUrls: ['./acknowledgement-list.component.css']
})
export class AcknowledgementListComponent extends BaseListClass implements OnInit {

  public fieldEnable  = true;
  public showTable = false;
  constructor(injector : Injector){
    super(injector);
  }

  ngOnInit(): void {
    this.initTables();
    this.initForm();
  }

  initTables() {
    this._reset(URLz.$);
    this._tbls[URLz.$].columns = ['S No','Actions', 'name','ack_receipt_number', 'total_amount'];
    this._tbls[URLz.$].endpoint = URLz.ACKNOWLEDGEMENT;
  }

  initForm() {
    this._fs._form = this._fb.group({
      bg: ['', this._vs._vals('Business Group')],
      le: ['', this._vs._vals('Legal Entity')],
      ou: ['', this._vs._vals('Operating Unit')],
      receipt_type:['',this._vs._vals('Receipt')],
      keyword:[''],
      search_by:['']
    });
  }

  SearcBy(check){
    this.fieldEnable = check;
    this._fs._form.get('keyword').patchValue('');
  }

  _on_Submit(){
    this._fs._form.markAllAsTouched();
    this._vs._submitted = true;
    const data = this._fs._form.getRawValue()
    if (this._fs._form.valid) {
      this._tbls[URLz.$].formFilter = this._fb.group({
            'receipt_type': [data.receipt_type],
            'bg': [data.bg],
            'le': [data.le],
            'ou': [data.ou],
            'keyword': [data.keyword],
            'search_by': [data.search_by]
      })
      this._http.get({
        endpoint: URLz.ACKNOWLEDGEMENT,
        query: {
          bg : data.bg,
          le : data.le,
          ou : data.ou,
          keyword : data.keyword,
          receipt_type : data.receipt_type,
          search_by : data.search_by
        }
      }).subscribe({
       next: (res: any) => {
          if(res.data){
            this._tbls[URLz.$].dataSource.data = res.data.records;
            res?.data?.records.length > 0 ? (this.showTable = true) : (
              this.showTable = false,
              this._swl.noDataFound()
            );
          }
        },
        error: (httpErrorResponse: HttpErrorResponse) => {
          this._vs._error_server(httpErrorResponse.error);
        }
      });
      this._vs._submitted = false;
    } else {
      return this._fs._form.valid;
    }
  }

  Switch(id ,rec_type){
    const url = this._router.serializeUrl(
      this._router.createUrlTree(['/blank/acknowledgement_color', { slipId: id , rec_t:rec_type , type:'ack'}])
    );
    window.open(url, '_blank');
  }

}
